* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.logo {
  width: 50px;
  height: 50px;
}

/* header */
#header {
  width: 100%;
  height: 70px;
  position: fixed;
  z-index: 99;
}

/* out Link */
.outLink {
  display: flex;
  justify-content: center;
  gap: 50px;
  margin: 50px 0px;
}
.outLink a {
  font-size: 25px;
  font-weight: bold;
  padding: 10px 20px;

  text-decoration: none;
  color: #fff;
  background: #333;
}
.outLink a:hover {
  color: #333;
  background: transparent;
}

/* main */
.main {
  width: 100%;
  height: 700px;
  background: url(./pic1.jpg) no-repeat center 90% / cover;

  display: flex;
  justify-content: center;
  align-items: center;
}
.main h4 {
  font-size: 100px;
  font-weight: bold;
  color: #fff;
  padding: 30px 100px;
  background: #000000a9;
}

/* product */
.product {
  margin-top: 50px;
}
.product h2 {
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  color: #333;
  padding-bottom: 10px;
  margin-bottom: 40px;
  border-bottom: 10px solid #333;
}
.product .product_row {
  padding: 10px 0px;
}
.product .product_list {
  position: relative;
}
.product h3 {
  width: 150px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;

  overflow: hidden;
}
.product span {
  width: 100%;
  padding: 10px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  display: block;
  background: #333;
  transform: translateY(-110%);
}
.product .product_list:hover span {
  transition: 0.3s;
  transform: translateY(0%);
}
.product h4 {
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  background: #333;
  padding: 10px;
}
.product p {
  color: #aaa;
  font-size: 16px;
  text-indent: 10px;
}
.product img {
  width: 100%;
  display: block;
  cursor: pointer;
}
.product img:hover {
  opacity: 0.4;
}

/* newProduct */
.newProduct {
  margin-top: 50px;
}
.newProduct h2 {
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  color: #777;
  padding-bottom: 10px;
  margin-bottom: 40px;
  border-bottom: 10px solid #777;
}
.newProduct .product_row {
  padding: 10px 0px;
}
.newProduct .product_list {
  position: relative;
}
.newProduct h3 {
  width: 150px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;

  overflow: hidden;
}
.newProduct span {
  width: 100%;
  padding: 10px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  display: block;
  background: #777;
  transform: translateY(-110%);
}
.newProduct .product_list:hover span {
  transition: 0.3s;
  transform: translateY(0%);
}
.newProduct h4 {
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  background: #777;
  padding: 10px;
}
.newProduct p {
  color: rgba(0, 83, 0, 0.596);
  font-size: 16px;
  text-indent: 10px;
}
.newProduct img {
  width: 100%;
  display: block;
  cursor: pointer;
}
.newProduct img:hover {
  opacity: 0.4;
}

/* data3 */
.data3 h2 {
  text-align: center;
  margin: 70px 0px;
}
.data3_list {
  width: 100%;
  display: flex;
  margin-bottom: 20px;
}
.data3_list img {
  width: 50%;
  display: block;
}
.data3_text {
  width: 50%;
  padding: 50px;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
}
.data3_text h4 {
  font-size: 30px;
  font-weight: bold;
}
.data3_text p {
  font-size: 20px;
}

/* detailPage */
.detailPage .image {
  padding: 0px;
}
.detailPage .inform {
  padding: 80px 30px;
  text-align: center;
  border: 1px solid #000;
}
.detailPage .inform h4 {
  font-size: 40px;
  font-weight: bold;
}
.detailPage .inform p {
  margin: 0px;
}
.detailPage .inform .color {
  font-size: 20px;
  font-weight: bold;
  color: #666;
  margin: 20px 0px;
}
.detailPage .inform .price {
  font-size: 30px;
  font-weight: bold;
  color: #666;
}
.detailPage .inform button {
  padding: 10px 50px;
  margin-top: 70px;

  font-weight: bold;
  color: #fff;
  background: #333;
  border-color: transparent;
  box-shadow: 0px 8px 0px #aaa;
  transform: translateY(-8px);

  transition: 0.3s;
}
.detailPage .inform button:hover {
  font-weight: bold;
  color: #000;
  background: #fff;
  border-color: #000;
  box-shadow: 0px 0px 0px #aaa;
  transform: translateY(0px);
}

/* bottomNav */
.bottomNav {
  justify-content: center;
  margin-top: 80px;
}
.content {
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}
